<style scoped lang="less">
    .page {
        text-align: left;
        padding: 30px;
    }

    .header {
        .input {
            min-width: 150px;
            max-width: 70%;
        }
        .header-col {
            height: 60px;
        }
        .header-col-select {
            width: 80%;
        }
    }

    .input {
        min-width: 150px;
        max-width: 70%;
    }

    .pagination {
        margin-top: 20px;
        width: 100%;
        text-align: center;
    }

    .num-input {
        width: 40%;
    }

</style>

<template>
    <div class="page">
        <div>
            <el-row class="header">
                <el-col class="filter-label" :span="2">规则编号:</el-col>
                <el-col :span="4">
                    <el-input class="filter-match" v-model="filter.id" placeholder="请输入"></el-input>
                </el-col>
                <el-col class="filter-label" :span="2"> 状态:</el-col>
                <el-col :span="4">
                    <el-select class="filter-match" v-model="filter.status" placeholder="请选择">
                        <el-option
                                v-for="item in statusOptions"
                                :key="item.value"
                                :label="item.label"
                                :value="item.value">
                        </el-option>
                    </el-select>
                </el-col>
                <el-col class="filter-label" :span="2"> 活动名称:</el-col>

                <el-col :span="4">
                    <el-input class="filter-match" v-model="filter.title" placeholder="请输入"></el-input>
                </el-col>
                <el-col class="filter-btn" :span="6">
                    <el-button icon="el-icon-search" type="primary" @click="loadData(1)">查询</el-button>
                    <el-button icon="el-icon-refresh" @click="ref()">重置</el-button>
                    <el-button icon="el-icon-plus" @click="add" type="primary">新建</el-button>
                </el-col>
            </el-row>
            <el-row class="filter">
                <el-col class="filter-label" :span="2"> 人数范围:</el-col>

                <el-col :span="4">
                    <el-input class="num-input" v-model="filter.min_register" placeholder="请输入"></el-input>
                    <div style="display: inline-block; width: 20%;text-align: center">～</div>
                    <el-input class="num-input" v-model="filter.max_register" placeholder="请输入"></el-input>
                </el-col>
                <el-col class="filter-label" :span="2"> 活动时间:</el-col>
                <el-col :span="4">
                    <el-date-picker
                            style="width: 100%"
                            type="datetimerange"
                            range-separator="~"
                            v-model="filter.active_time"
                            start-placeholder="开始"
                            value-format="yyyy-MM-dd HH:mm:ss"
                            format="yyyy-MM-dd"
                            end-placeholder="结束">
                    </el-date-picker>
                </el-col>

                <el-col class="filter-label" :span="2"> 报名时间:</el-col>
                <el-col :span="4">
                    <el-date-picker
                            style="width: 100%"
                            type="datetimerange"
                            range-separator="~"
                            v-model="filter.registration_time"
                            start-placeholder="开始"
                            value-format="yyyy-MM-dd HH:mm:ss"
                            format="yyyy-MM-dd"
                            end-placeholder="结束">
                    </el-date-picker>
                </el-col>
            </el-row>
        </div>
        <el-table
                :data="data.data"
                stripe
                style="width: 100%">
            <el-table-column
                    prop="id"
                    label="ID">
            </el-table-column>
            <el-table-column
                    prop="title"
                    label="标题">
            </el-table-column>
            <el-table-column
                    prop="begin_time"
                    label="开始时间">
            </el-table-column>
            <el-table-column
                    prop="end_time"
                    label="结束时间">
            </el-table-column>
            <el-table-column
                    label="人数限制">
                <template slot-scope="scope">
                    <span>{{scope.row.limit==0?'无限制':scope.row.limit}}</span>

                </template>
            </el-table-column>
            <el-table-column
                    prop="apply_time"
                    label="活动时间">
            </el-table-column>
            <el-table-column
                    label="操作">
                <template slot-scope="scope">
                    <el-button @click="handleClick(scope.row)" type="text" size="small">详细</el-button>
                    <el-button @click="checkApply(scope.row)" type="text" size="small">报名审核</el-button>
                    <el-button type="text" @click="del(scope)" size="small">删除</el-button>
                </template>
            </el-table-column>
        </el-table>
        <div class="pagination">
            <el-pagination
                    background
                    layout="total,prev, pager, next,jumper"
                    :page-count="data.last_page"
                    @current-change="loadData"
            >
            </el-pagination>
        </div>
    </div>
</template>

<script>
    import api from '../../js/api/activity'

    export default {
        name: "activity",
        data() {
            return {
                dialog: {
                    dialogVisible: false,
                    content: "",
                },
                statusOptions: [{label: '禁用', value: -1}, {label: '全部', value: 1}, {
                    label: '预告期',
                    value: 2
                }, {label: '报名中', value: 3}, {label: '报名结束', value: 4}, {label: '活动结束', value: 5}],

                filter: {
                    id: null,
                    status: null,
                    title: null,
                    min_register: null,
                    max_register: null,
                    jump_page: null,
                    active_begin: null,
                    active_end: null,
                    active_time: null,
                },
                data: {}
            }
        },
        mounted() {
            this.loadData(1)
        },
        methods: {

            ref() {
                this.filter = {
                    id: null,
                    status: null,
                    title: null,
                    min_register: null,
                    max_register: null,
                    jump_page: null,
                    active_begin: null,
                    active_end: null,
                    active_time: null,
                };
                this.loadData(1)
            },
            loadData(page) {
                this.filter.jump_page = page;

                if (this.filter.active_time != null && this.filter.active_time.length === 2) {
                    this.filter.active_begin = this.filter.active_time[0];
                    this.filter.active_end = this.filter.active_time[1]
                } else {
                    this.filter.active_begin = null;
                    this.filter.active_end = null;
                }
                if (this.filter.registration_time != null && this.filter.registration_time.length === 2) {
                    this.filter.registration_begin = this.filter.registration_time[0];
                    this.filter.registration_end = this.filter.registration_time[1]
                } else {
                    this.filter.registration_begin = null;
                    this.filter.registration_end = null;
                }
                if (this.filter.min_register == null) {
                    this.filter.min_register = null;
                } else {
                    this.filter.min_register = parseInt(this.filter.min_register)

                }
                if (this.filter.max_register == null) {
                    this.filter.max_register = null;
                } else {
                    this.filter.max_register = parseInt(this.filter.max_register)
                }

                api.entry(this.filter, res => {
                    this.data = res.data.data;
                })

            },
            filterOpen() {
                this.filter.isOpen = !this.filter.isOpen;
            },
            add() {
                this.$router.push({
                    name: "activityAdd",
                });
            },
            del(scope) {
                let _this = this;
                this.$confirm('此操作将永久删除' + scope.row.title + ', 是否继续?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    api.delete({id: scope.row.id}, () => {
                        this.$message.success('删除成功');
                        _this.loadData(1);
                    })

                }).catch(() => {
                    this.$message({
                        type: 'info',
                        message: '已取消删除'
                    });
                });
            },
            checkApply(item) {
                this.$router.push({
                    name: "activityApply",
                    query: {
                        id: item.id,
                    },
                });
            },
            handleClick(item) {
                this.$router.push({
                    name: "activityAdd",
                    query: {
                        id: item.id,
                    },
                });
            },
        }
    }
</script>

